
import Swiper from 'swiper'
import gsap from 'gsap'
import ScrollTrigger from "gsap/ScrollTrigger";


import '../sass/main.sass'
import '../index.html'

function init(){

    // GSAP
    // MAIN-SCREEN
    var tl = gsap.timeline();
        tl.fromTo(".header", {opacity: 0}, {opacity: 1}, 1);
        tl.fromTo(".main-screen", {opacity: 0}, {opacity: 1}, 2);
        tl.fromTo(".main-screen__title", {opacity: 0}, {opacity: 1}, 3);
        tl.fromTo(".main-screen__subtitle", {opacity: 0}, {opacity: 1}, 4);
        tl.fromTo(".buttons-wrap__btn", {opacity: 0}, {opacity: 1}, 5);

    gsap.registerPlugin(ScrollTrigger);
    gsap.set(".scroll-trigger", {opacity: 0, y: 24});

    ScrollTrigger.batch(".scroll-trigger", {
      onEnter: batch => gsap.to(batch, {opacity: 1, y: 0, stagger: 0.15}),
      onLeave: batch => gsap.to(batch, {opacity: 0, y: 24}),
      onEnterBack: batch => gsap.to(batch, {opacity: 1, y: 0, stagger: 0.15}),
      onLeaveBack: batch => gsap.to(batch, {opacity: 0, y: 24}),
    
      start: "start center",
      end: "100% center",
    });




    // HEADER__SCROL
    function scrollLine(){
        window.addEventListener('scroll', function() {
            let headerLine = document.querySelector('.header__scroll');
            let wintop = window.pageYOffset,
                docheight = document.documentElement.scrollHeight,
                winheight = document.documentElement.clientHeight
            let scrolled = (wintop / (docheight - winheight)) * 100;
            headerLine.style.width = scrolled + "%";
        });
    }
    scrollLine();

    // BURGER
    function burger(){
        let burger = document.querySelector('#burger');
        let headerMenu = document.querySelector('.header-mobile__nav');

        burger.addEventListener("click", function(){
            headerMenu.classList.toggle('header-mobile__nav_active');
        });
    }
    burger();

    // SLIDER
    function sectionSlider(){
        const slider = new Swiper('.section-slider__slider', {
            spaceBetween: 25,
            loop: true,
            lazy: true,
            autoplay: {
                delay: 2500,
                disableOnInteraction: false,
                stopOnLast: true,
            },
            navigation: {
                nextEl: '.swiper-button-next',
                prevEl: '.swiper-button-prev',
            },
            breakpoints: {
                320: {
                    slidesPerView: 2,
                    spaceBetween: 25,
                    
                },
                1024: {
                    slidesPerView: 3.3,
                    spaceBetween: 25
                },
                1600: {
                    slidesPerView: 4.3,
                }
            }
        });
    }
    sectionSlider()

    // TABS
    function tabs(){
        const tabsBtn   = document.querySelectorAll(".tabs__tab");
        const tabsItems = document.querySelectorAll(".tabs__content");

        tabsBtn.forEach(onTabClick);

        function onTabClick(item) {
            item.addEventListener("click", function() {
                let currentBtn = item;
                let tabId = currentBtn.getAttribute("data-tab");
                let currentTab = document.querySelector(tabId);

                if( ! currentBtn.classList.contains('tabs__tab_active') ) {
                    tabsBtn.forEach(function(item) {
                        item.classList.remove('tabs__tab_active');
                    });
            
                    tabsItems.forEach(function(item) {
                        item.classList.remove('tabs__content_active');
                    });
            
                    currentBtn.classList.add('tabs__tab_active');
                    currentTab.classList.add('tabs__content_active');
                }
            });
            document.querySelector('.tabs__tab').click();
        }
    }
    tabs();

}

window.onload = init;